.process {
	padding: 146px 0 55px;
	flex-wrap: wrap;
	align-items: center;
	display: flex;
	@include md {
		padding: 90px 0 45px; }
	&__img {
		@include fs(56%);
		@include md {
			@include fs(100%); }
		img {
			max-width: 100%;
			display: block;
			@include md {
				margin: 0 auto; } } }
	&__content {
		padding-left: 54px;
		@include fs(44%);
		@include md {
			margin-bottom: 50px;
			padding-left: 0;
			@include fs(100%);
			order: -1; } }
	&__title {
		margin-bottom: 17px; }
	&__list {
		&:not(:first-child) {
			margin-top: 39px; } } }
.list-square {
	font-size: 17px;
	line-height: 1.176;
	&__item {
		position: relative;
		padding-left: 24px;
		display: block;
		&:before {
			position: absolute;
			content: '';
			top: 3px;
			left: 0;
			width: 13px;
			height: 13px;
			background-color: $blue; }
		&:not(:last-child) {
			margin-bottom: 22px; }
		&:last-child {
			margin-top: 41px; } }
	&__title {
		@include f(17px,700,1.176); } }
