.form {
	position: relative;
	text-align: center;
	&_black {
		padding: 27px 35px 31px 27px;
		color: $white;
		border-radius: 19px;
		background-color: #364049;
		.contacts & {
			@include sm {
				margin: 0 -30px; } }
		&:before {
			position: absolute;
			content: '';
			top: 100%;
			left: 50%;
			transform: translateX(-50%);
			width: 284px;
			height: 27px;
			opacity: .33;
			filter: blur(10px);
			background-color: #4A4A4A; } }
	&__title {
		margin-bottom: 25px;
		.form_black & {
			margin-bottom: 18px; } }
	&__footer {
		margin-top: 12px;
		@include sm {
			margin-top: 4px; } } }
.form-control {
	&_blue {
		input,
		textarea {
			border: 1px solid #147EEC; } }
	&:not(:first-child) {
		margin-top: 14px;
		@include sm {
			margin-top: 12px; } }
	input,
	textarea {
		padding: 14px 19px;
		height: 43px;
		width: 100%;
		@include fmt;
		color: #364049;
		@include f(12px,400,1.25);
		border-radius: 20px;
		@include sm {
			height: 50px;
			border-radius: 25px; }
		&::placeholder {
			color: rgba(#364049, .57); }
		&:focus {
			&::placeholder {
				font-size: 0; } } }
	textarea {
		padding-top: 19px;
		height: 176px;
		resize: none;
		@include sm {
			height: 152px; } }
	&_sm {
		&:not(:first-child) {
			margin-top: 13px; }
		input {
			height: 40px; }
		textarea {
			height: 160px; } } }
