.about {
	padding: 118px 0 94px;
	@include md {
		padding: 90px 0; }
	@include sm {
		padding: 55px 0 30px; }
	&__row {
		margin: 0 -32px -66px;
		@include md {
			margin-bottom: -45px; } }
	&__col {
		padding: 0 32px;
		margin-bottom: 66px;
		@include md {
			margin-bottom: 45px; }
		@include sm {
			margin-bottom: 30px; } } }
.aboutitem {
	position: relative;
	padding: 33px 69px 29px 54px;
	height: 100%;
	border-radius: 20px;
	border: 1px solid #147EEC;
	@include sm {
		padding: 30px; }
	&.is-blue {
		color: $white;
		background-color: $blue; }
	&:before {
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		bottom: 0;
		width: 30px;
		border-radius: 17px 0 0 17px;
		background-color: #147EEC;
		@include sm {
			display: none; } }
	&__figure {
		position: absolute;
		right: 69px;
		top: 0;
		width: 46px;
		transform: translateY(-50%);
		@include sm {
			display: none; }
		img {
			display: block; } }
	&__title {
		margin-bottom: 22px;
		@include f(24px,700,1.2);
		@include sm {
			margin-bottom: 16px; }
		@include sm {
			margin-bottom: 10px; } } }
