html {
	&.is-open-menu {
		overflow: scroll; } }

body {
	@include f(17px,500,1.176);
	@include fmt;
	@include font-smoothing;
	color: $textColor;
	min-width: 320px;
	background-color: $white;
	@include sm {
		font-size: 14px;
		line-height: 1.5; }
	.is-open-menu & {
		position: fixed;
		height: 100vh;
		width: 100%;
		overflow: hidden; } }

.title-xl {
	@include f(56px,600,1.01);
	@include md {
		font-size: 42px; }
	@include sm {
		@include f(34px,600,1.3125); } }

.title-lg {
	@include f(45px,600,1.13);
	@include sm {
		@include f(32px,600,1.3125); } }
.title {
	@include f(36px,500,1.22);
	@include sm {
		@include f(24px,500,1.2); } }
.title-md {
	@include f(31px,600,1.22);
	@include sm {
		@include f(24px,600,1.2); } }
.color-blue {
	color: $blue; }
.color-orange {
	color: #FF5C39; }
.color-green {
	color: #63D50A; }

.info {
	padding: 63px 0 70px;
	color: #505D68;
	@include md {
		padding: 45px 0; }
	@include sm {
		padding: 30px 0; }
	&__inner {
		margin: 0 auto;
		max-width: 1011px; } }
